import React from 'react'
import { Container, Row, Col } from 'reactstrap'

const Benefits = ({ benefitList }) => (
    <section className="gym benefit " id="plan">
        <Container>
            <h3 className="oftHeading text-dark">Membership Benefits</h3>
            <Row>
                {
                    benefitList.map((x, i) => (
                        <Col lg="4" md="4" className="offset-md-2 offset-lg-0" key={i}>
                            <div>
                                <center>
                                    <img src="/assets/images/OTF/icons/heatl1.png" alt="img" />
                                </center>
                            </div>
                            <p className='p-light oftsubHeading text-center mt-3'>
                                Easy online booking
                            </p>
                        </Col>
                    ))
                }

                <Col lg="4" md="4" className="offset-md-2 offset-lg-0">
                    <div>
                        <center>
                            <img src="/assets/images/OTF/icons/heatl1.png" alt="img" />
                        </center>
                    </div>
                    <p className='p-light oftsubHeading text-center mt-3'>
                        Nationwide studio access*
                    </p>
                </Col>
                <Col lg="4" md="4" className="offset-md-2 offset-lg-0"
                >
                    <div>
                        <center>
                            <img src="/assets/images/OTF/icons/heatl1.png" alt="img" />
                        </center>
                    </div>
                    <p className='p-light oftsubHeading text-center mt-3'>
                        Month-to-month memberships
                    </p>
                </Col>
            </Row>
        </Container>
    </section>
)


export default Benefits;